        //
        lastReceiveSuccessTime: new Date('2017/1/1'),
        lastSetTime: new Date('2017/1/1'),
        //
        DataLen: 0,
        //
        ReceiveDataLen: 0,
        ///
        CurCmd: Cmd.sports,
        ///
        CurHandler: null,
        //
        taskQueueTimeLast: new Date('2017/1/1'),
        LCDDisplayDataHandlerSuccessFlag: false,
        SleepDataHandlerSuccessFlag: false,
